.ui-btn {
  display: inline-block;
  vertical-align: middle;
  height: 62px;
  line-height: 62px;
  letter-spacing: 0.64px;
  max-width: 100%;
  position: relative;
  border: none;
  border-radius: 30px;
  padding-left: 32px;
  padding-right: 32px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  background: linear-gradient(180deg, #BD5B9E 0%, #A5417D 100%);
  color: #FFF;
  text-align: center;
  font-size: 18px;
  font-size: 1.125rem;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.3s ease-in-out;
  text-transform: uppercase;
  user-select: none;
}
.ui-btn svg {
  margin-left: 12px;
  width: 14px;
  height: 14px;
  margin-right: 6px;
}
@media (max-width: 1680px) {
  .ui-btn {
    height: 50px;
    line-height: 50px;
    font-size: 15px;
    font-size: 0.9375rem;
  }
}
@media (max-width: 960px) {
  .ui-btn {
    height: 46px;
    line-height: 46px;
    font-size: 13px;
    font-size: 0.8125rem;
  }
}
@media (max-width: 599px) {
  .ui-btn {
    height: 50px;
    line-height: 50px;
    font-size: 15px;
    font-size: 0.9375rem;
  }
}
.ui-btn.ui-btn-small {
  height: 41px;
  line-height: 41px;
  padding-left: 20px;
  padding-right: 20px;
  font-size: 16px;
  font-size: 1rem;
}
.ui-btn.ui-btn-green {
  background: linear-gradient(180deg, #319DAC 0%, #278FAF 100%);
}
.ui-btn.ui-btn-transparent {
  font-size: 16px;
  font-size: 1rem;
  background: transparent;
  color: #319DAC;
  height: unset;
  line-height: unset;
  letter-spacing: 0.64px;
  padding: 0;
  width: max-content;
}
.ui-btn.ui-btn-transparent:hover {
  color: #4C4D6E;
  background: none;
}
.ui-btn.ui-btn-transparent:hover svg {
  transition: all 0.2s;
}
.ui-btn.ui-btn-transparent:hover svg path {
  stroke: #4C4D6E;
}
.ui-btn.ui-btn-transparent svg {
  margin-left: 0;
  margin-right: 4px;
}
.ui-btn.ui-btn-tab {
  background: #FFF;
  color: #343544;
}
.ui-btn.ui-btn-tab.active {
  background: linear-gradient(180deg, #BD5B9E 0%, #A5417D 100%);
  color: #FFF;
}
.ui-btn.ui-btn-tab:hover {
  background: linear-gradient(180deg, #BD5B9E 0%, #A5417D 100%);
  color: #FFF;
}
.ui-btn:hover {
  box-shadow: none;
  background: #4C4D6E;
}

.ui-field {
  border-radius: 30px;
  overflow: hidden;
  position: relative;
  display: flex;
  align-items: center;
  width: 400px;
}
.ui-field label.error {
  font-size: 12px;
  font-size: 0.75rem;
  position: absolute;
  color: #BD5B9E;
  left: 30px;
  bottom: 0;
  padding: 2px 4px;
}
.ui-field .input-icon {
  position: absolute;
  right: 21px;
}
.ui-field .ui-input {
  height: 62px;
  color: #343544;
  font-size: 18px;
  font-size: 1.125rem;
  font-weight: 400;
  letter-spacing: 0.36px;
  background-color: #CDF3F9;
  width: 100%;
  outline: none;
  border: none;
  border: none;
  padding: 17px 32px;
  outline: none;
}
.ui-field .ui-input.error {
  margin-bottom: 0px;
}
.ui-field .ui-input.small {
  height: 40px;
}
.ui-field .ui-input::placeholder {
  color: #51647B;
}
.ui-field .ui-input.error {
  border-color: #BD5B9E;
}
.ui-field input:invalid + span:before {
  content: attr(placeholder);
  position: relative;
  color: #51647B;
}
.ui-field input:invalid + span:after {
  content: "*";
  position: relative;
  color: #BD5B9E;
}

