.wrapper {
  display: flex;
  flex-direction: column;
}

.header {
  position: fixed;
  z-index: 10;
  width: 100%;
}
.header-top {
  background-color: #319DAC;
}
.header-top-logo {
  width: 220px;
  height: 45px;
  margin-top: auto;
  margin-bottom: auto;
}
.header-top-wrapper {
  display: flex;
  align-items: center;
  height: 80px;
}
.header-top-contact {
  display: flex;
  align-items: center;
  margin-left: auto;
  margin-right: 40px;
  font-size: 18px;
  font-size: 1.125rem;
  letter-spacing: 0.72px;
}
.header-top-contact .address {
  display: flex;
  align-items: center;
  gap: 12px;
  color: #fff;
  margin-right: 40px;
}
.header-top-contact .phone {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-right: 20px;
}
.header-top-contact .phone a {
  display: inherit;
  color: #fff;
}
.header-top-contact .phone a.viber, .header-top-contact .phone a.tel {
  width: 32px;
  height: 32px;
}
.header-top-contact .socials {
  display: flex;
  align-items: center;
}
.header-top-contact .socials a {
  width: 32px;
  height: 32px;
  display: inherit;
}
.header-bottom {
  background-color: #BD5B9E;
}
.header-bottom .header-nav {
  display: flex;
  align-items: center;
  gap: 60px;
  color: #fff;
  height: 60px;
}
.header-bottom .header-nav-item {
  height: 100%;
}
.header-bottom .header-nav-item:hover ul {
  height: max-content;
  opacity: 1;
  transform: translateY(0);
  overflow: initial;
  transition: all 0.8s;
}
.header-bottom .header-nav-item:hover .trigger-arrow {
  transform: rotate(180deg);
}
.header-bottom .header-nav-item .trigger {
  height: 100%;
  display: flex;
  align-items: center;
  gap: 6px;
  cursor: pointer;
}
.header-bottom .header-nav-item .trigger-text {
  height: 100%;
  font-size: 18px;
  font-size: 1.125rem;
  font-weight: 500;
  text-transform: uppercase;
  line-height: 60px;
  letter-spacing: 0.72px;
  position: relative;
}
.header-bottom .header-nav-item .trigger-text::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  height: 8px;
  width: 0%;
  background-color: #CDF3F9;
  border-radius: 4px 4px 0px 0px;
  transition: width 0.3s ease-in-out;
}
.header-bottom .header-nav-item .trigger-text:hover::after {
  width: 100%;
}
.header-bottom .header-nav-item .trigger-text.active {
  position: relative;
}
.header-bottom .header-nav-item .trigger-text.active::after {
  width: 100%;
}
.header-bottom .header-nav-item .trigger-arrow {
  transition: all 0.4s;
}
.header-bottom .header-nav-item ul {
  position: absolute;
  top: 50px;
  height: 0;
  opacity: 0;
  transform: translateY(-10px);
  overflow: hidden;
  background-color: #fff;
  box-shadow: 0px 6px 30px 0px rgba(43, 47, 137, 0.15);
  border-radius: 12px;
  padding: 20px 20px;
  padding-right: 0;
  transition: all 0.3s;
  z-index: 3;
}
.header-bottom .header-nav-item ul li {
  margin-right: 20px;
}
.header-bottom .header-nav-item ul li a {
  width: 100%;
  color: #222340;
  padding: 12px 20px;
  display: inline-block;
  font-size: 18px;
  font-size: 1.125rem;
  font-weight: 500;
  letter-spacing: 0.72px;
  text-transform: uppercase;
  transition: all 0.5s;
}
.header-bottom .header-nav-item ul li a:hover {
  color: #fff;
  background-color: #319DAC;
  border-radius: 8px;
}
.header-bottom .header-nav-item .sub-menu {
  margin-right: -20px;
}
.header-bottom .header-nav-item .sub-menu-wrapper {
  position: relative;
}
.header-bottom .header-nav-item .sub-menu a {
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 0.4s;
}
.header-bottom .header-nav-item .sub-menu a:hover {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.header-bottom .header-nav-item .sub-menu a:hover svg {
  visibility: hidden;
}
.header-bottom .header-nav-item .sub-menu ul {
  display: none;
  position: absolute;
  right: -20px;
  top: -20px;
  transform: translateX(100%);
  background-color: #fff;
  padding-left: 0;
