.accordion-head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  cursor: pointer;
  border-bottom: 2px solid #319DAC;
}
.accordion-head .title {
  font-size: 24px;
  font-size: 1.5rem;
  font-weight: 600;
  line-height: 133.333%;
}
.accordion-head .icon {
  position: relative;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: var(--gradient-gradient-green, linear-gradient(180deg, #319DAC 0%, #278FAF 100%));
  transition: all 0.5s;
}
.accordion-head .icon.open {
  background: #CDF3F9;
}
.accordion-head .icon.open svg path {
  transition: all 0.5s;
  stroke: #319DAC;
}
.accordion-head .icon.open svg path:first-child {
  display: none;
}
.accordion-head .icon svg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.accordion-content {
  max-height: 0;
  overflow: hidden;
  transition: all 0.5s;
}
.accordion-content.open {
  max-height: max-content;
}

